<template>
  <div class="bill-cnt">
    <b-card>
      <!-- title -->
      <h4 class="title">دریافت صورتحساب بر اساس:</h4>
      <!-- person name -->
      <b-form-group class="cs-form-group" v-if="type == 'person'">
        <div class="label-cnt">
          <b-form-checkbox v-model="checkOne" class="custom-control-primary" @change="checkOne ? selectedUser : (selectedUser = null)" />
          <p class="label">نام شخص</p>
        </div>
        <v-select :searchable="true" dir="rtl" :clearable="false" v-model="selectedUser" :options="users" :class="{ 'no-select': !checkOne }" class="select-size-lg" label="name">
        </v-select>
      </b-form-group>

      <!-- floor -->
      <b-form-group class="cs-form-group">
        <div class="label-cnt">
          <b-form-checkbox v-model="checkTwo" @change="checkTwo ? selectedFloor : (selectedFloor = null)" class="custom-control-primary" />
          <p class="label">طبقه</p>
        </div>
        <v-select
          :searchable="false"
          :clearable="false"
          v-model="selectedFloor"
          :options="floors"
          dir="rtl"
          :class="{ 'no-select': !checkTwo }"
          class="select-size-lg"
          label="floor_name"
        >
        </v-select>
      </b-form-group>

      <!-- from floor / to floor -->
      <b-form-group class="cs-form-group">
        <div class="label-cnt">
          <b-form-checkbox v-model="checkThree" @change="statusCheck('unit')" class="custom-control-primary" />
          <p class="label">واحد</p>
        </div>

        <div class="d-flex align-items-center justify-content-between" :class="{ 'no-select': !checkThree }">
          <div class="d-flex align-items-center w-100 dbl-vselect-item">
            <span>از واحد</span>
            <v-select :searchable="false" :clearable="false" v-model="selectedFromUnit" :options="units" dir="rtl" class="select-size-lg" label="unit_name"> </v-select>
          </div>

          <div class="d-flex align-items-center justify-content-between w-100 dbl-vselect-item">
            <span>تا واحد</span>
            <v-select :searchable="false" :clearable="false" v-model="selectedToUnit" :options="units" dir="rtl" class="select-size-lg" label="unit_name"> </v-select>
          </div>
        </div>
      </b-form-group>

      <!-- Debt Heading -->
      <b-form-group class="cs-form-group">
        <div class="label-cnt">
          <b-form-checkbox v-model="checkFour" @change="checkFour ? selectedDebtCategory : (selectedDebtCategory = null)" class="custom-control-primary" />
          <p class="label">سرفصل بدهی</p>
        </div>
        <v-select
          :searchable="false"
          :clearable="false"
          v-model="selectedDebtCategory"
          :options="debtCategoryList"
          dir="rtl"
          :class="{ 'no-select': !checkFour }"
          class="select-size-lg"
          label="debt_category_name"
        >
        </v-select>
      </b-form-group>

      <!-- from date / to date -->
      <b-form-group class="cs-form-group">
        <div class="label-cnt">
          <b-form-checkbox v-model="checkFive" @change="statusCheck('date')" class="custom-control-primary" />
          <p class="label">تاریخ</p>
        </div>

        <div class="d-flex align-items-center justify-content-between" :class="{ 'no-select': !checkFive }">
          <div class="d-flex align-items-center">
            <span>از تاریخ</span>
            <input type="text" class="custom-input" />
            <date-picker color="#7468F0" auto-submit v-model="onDate" custom-input=".custom-input" />
          </div>

          <div class="d-flex align-items-center justify-content-between">
            <span>تا تاریخ</span>
            <input type="text" class="custom-input-nd" />
            <date-picker color="#7468F0" auto-submit v-model="toDate" custom-input=".custom-input-nd" />
          </div>
        </div>
      </b-form-group>

      <div class="btn-action">
        <b-button block variant="primary" @click="submit()"> دریافت صورتحساب </b-button>
      </div>
    </b-card>
  </div>
</template>

<script>
//** datePicker */
import VuePersianDatetimePicker from 'vue-persian-datetime-picker'

//** v-select */
import vSelect from "vue-select";

//** APIs */
import { getUser, getFloor, getBill } from '/src/api/billServices'
import { getDebtCategory } from '/src/api/costsServices'

//**bootstrap-vue tools */
import {
  BAvatar,
  BCard,
  BFormGroup,
  BFormInput,
  BFormCheckbox,
  BFormSelect,
  BButton
} from "bootstrap-vue";
export default {
  name: "BillPerson",
  props: {
    type: {
      type: String,
      default: "person",
    }
  },
  components: {
    BAvatar,
    BCard,
    BFormGroup,
    BFormInput,
    BFormCheckbox,
    BFormSelect,
    vSelect,
    BButton,
    datePicker: VuePersianDatetimePicker,
  },
  data() {
    return {
      //** User */
      selectedUser: null,
      users: [],

      //** floor */
      selectedFloor: null,
      floors: [],

      //** from / to floor */
      selectedFromUnit: null,
      selectedToUnit: null,

      //**get DebtCategory */
      selectedDebtCategory: null,
      debtCategoryList: [],

      //**on / to Date */
      onDate: '',
      toDate: '',

      //** chek */
      checkOne: false,
      checkTwo: false,
      checkThree: false,
      checkFour: false,
      checkFive: false,

      link: null,
    }
  },
  methods: {
    //** get user */
    async userInfo() {
      try {
        let { data } = await getUser()
        this.users = data.list.filter((item) => {
          return item.name
        })

      } catch (error) {
        console.error(error);
      }
    },

    //** get floor */
    async floorInfo() {
      try {
        let { data } = await getFloor()
        this.floors = data.list
      } catch (error) {
        console.error(error)
      }
    },

    //** get DebtCategory */
    async debtCategoryInfo() {
      try {
        let { data } = await getDebtCategory({ data: { debt_type: [1, 2, 4] } })
        this.debtCategoryList = data.debtor_list.list
      } catch (error) {
        console.error(error)
      }
    },
    statusCheck(check) {
      if (check == 'unit') {
        if (!this.checkThree) {
          this.selectedFromUnit = null
          this.selectedToUnit = null
        }
      }
      else {
        if (!this.checkFive) {
          this.onDate = null
          this.toDate = null
        }
      }


    },
    async submit() {
      try {
        let { data } = await getBill({
          type: this.type,
          user_id: this.selectedUser ? this.selectedUser.user_id : null,
          floor_id: this.selectedFloor ? this.selectedFloor.floor_id : null,
          from_unit: this.selectedFromUnit ? this.selectedFromUnit : null,
          to_unit: this.selectedToUnit ? this.selectedToUnit : null,
          debt_category_id: this.selectedDebtCategory ? this.selectedDebtCategory : null,
          from_date: this.onDate ? this.onDate : null,
          to_date: this.toDate ? this.toDate : null
        });

        const unit = {
           value: this.selectedFromUnit ? `${this.selectedFromUnit.unit_name} _ ${this.selectedToUnit? this.selectedToUnit.unit_name : ''}` : "",
        }

        const message = {
          type: this.type,
          link: data.link,
          pageTitle: 'گزارشات',
          pageDec: 'لورم ایپسوم متن ساختگی با تولید سادگی نامفهوم از صنعت چاپ و با استفاده از طراحان گرافیک است',
          successMsg: 'صورتحساب با موفقیت ساخته شد',
          data: [
            { key: "طبقه", value: this.selectedFloor ? this.selectedFloor.floor_name : "" },
            { key: "واحد", value: unit.value },
            { key: "سرفصل بدهی", value: this.selectedDebtCategory ? this.selectedDebtCategory.debt_category_name : "" },
            { key: "تاریخ", value: `${this.onDate} - ${this.toDate}` },
          ]
        }
        if (this.type == 'person') {
          message.data.push(
            { key: "نام شخص", value: this.selectedUser ? this.selectedUser.name : "" }
          );

          // swtich positions
          let tmp= message.data[0]
          message.data[0]= message.data[4]
          message.data[4]= tmp
        }
        this.$store.commit("bill/setSuccessObject", message)
        this.$router.push({ name: 'success-page' })
      }
      catch (e) {
        console.log(e)
      }
    }
  },
  mounted() {
    this.userInfo()
    this.floorInfo()
    this.debtCategoryInfo()
  },
  computed: {
    units() {
      if (this.selectedFloor) return this.selectedFloor.unit_list

      let units = []
      this.floors.map((floor) => {
        floor.unit_list.map((unit) => {
          units.push(unit)
        })
      })
      return units
    }
  }
}
</script>

